.topbarContainer {
  width: 100%;
  height: 50px;
  background-color: #1877f5;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}
.topbarContainer .topbarLeft {
  flex: 3;
}
.topbarContainer .topbarLeft .logo {
  margin-left: 20px;
  font-size: 24px;
  font-weight: bolder;
  cursor: pointer;
}
.topbarContainer .topbarCenter {
  flex: 5;
  background-color: #fff;
  width: 100px;
  border-radius: 30px;
}
.topbarContainer .topbarCenter .search {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
}
.topbarContainer .topbarCenter .search .searchIcon {
  font-size: 20px !important;
  cursor: pointer;
  margin-left: 10px;
  color: #000;
}
.topbarContainer .topbarCenter .search input {
  width: 80%;
  border: none;
  margin-left: 10px;
  outline: none;
  font-size: 15px;
}
.topbarContainer .topbarCenter .search input::-moz-placeholder {
  color: #999;
}
.topbarContainer .topbarCenter .search input:-ms-input-placeholder {
  color: #999;
}
.topbarContainer .topbarCenter .search input::placeholder {
  color: #999;
}
.topbarContainer .topbarRight {
  flex: 4;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.topbarContainer .topbarRight .topbarLinks {
  display: flex;
  align-items: center;
}
.topbarContainer .topbarRight .topbarLinks .topbarLink {
  font-size: 18px;
  margin-right: 10px;
  cursor: pointer;
}
.topbarContainer .topbarRight .topbarIcons {
  display: flex;
  color: #fff;
}
.topbarContainer .topbarRight .topbarIcons .topbarIconItems {
  margin-right: 15px;
  cursor: pointer;
  font-size: 20px;
  position: relative;
  display: flex;
  align-items: center;
}
.topbarContainer .topbarRight .topbarIcons .topbarIconItems .topbarIconBadge {
  width: 16px;
  height: 16px;
  background-color: red;
  position: absolute;
  top: -5px;
  right: -5px;
  font-size: 16px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.topbarContainer .topbarRight img {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  border-radius: 50%;
  -o-object-fit: cover;
     object-fit: cover;
  cursor: pointer;
}/*# sourceMappingURL=Topbar.css.map */