.friendDetails {
  display: flex;
  align-items: center;
  margin: 15px 0;
  cursor: pointer;
  .profile {
    position: relative;
    img {
      width: 48px;
      height: 48px;
      border-radius: 50%;
      object-fit: cover;
    }
    span {
      position: absolute;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      top: -3px;
      right: 3px;
      border: 2px solid white;
      background-color: greenyellow;
    }
  }
  span {
    margin-left: 10px;
    font-size: 20px;
    font-weight: bold;
  }
}
