// ! variables
$mainColor: #1877f5;
$white: #fff;
.topbarContainer {
  width: 100%;
  height: 50px;
  background-color: $mainColor;
  color: $white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
  //* Left haeder with logo
  .topbarLeft {
    flex: 3;
    .logo {
      margin-left: 20px;
      font-size: 24px;
      font-weight: bolder;
      cursor: pointer;
    }
  }

  //* Center Header
  .topbarCenter {
    flex: 5;
    background-color: $white;
    width: 100px;
    border-radius: 30px;

    .search {
      width: 100%;
      height: 30px;
      display: flex;
      align-items: center;
      .searchIcon {
        font-size: 20px !important;
        cursor: pointer;
        margin-left: 10px;
        color: #000;
      }

      input {
        width: 80%;
        border: none;
        margin-left: 10px;
        outline: none;
        font-size: 15px;
        &::placeholder {
          color: #999;
        }
      }
    }
  }

  //* right Header
  .topbarRight {
    flex: 4;
    display: flex;
    justify-content: space-around;
    align-items: center;
    .topbarLinks {
      display: flex;
      align-items: center;

      .topbarLink {
        font-size: 18px;
        margin-right: 10px;
        cursor: pointer;
      }
    }
    .topbarIcons {
      display: flex;
      color: #fff;

      .topbarIconItems {
        margin-right: 15px;
        cursor: pointer;
        font-size: 20px;
        position: relative;
        display: flex;
        align-items: center;

        .topbarIconBadge {
          width: 16px;
          height: 16px;
          background-color: red;
          position: absolute;
          top: -5px;
          right: -5px;
          font-size: 16px;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
    img {
      width: 34px;
      height: 34px;
      display: flex;
      align-items: center;
      border-radius: 50%;
      object-fit: cover;
      cursor: pointer;
    }
  }
}
