.post {
  margin: 20px 0;
  box-shadow: 0 0 16px -8px rgba(0, 0, 0, 0.68), 0 0 16px -8px rgba(0, 0, 0, 0.68);
  width: 100%;
  border-radius: 10px;
  margin: 15px;
}
.post .postTop {
  display: flex;
  justify-content: space-between;
  margin: 10px;
  padding: 10px;
  align-items: center;
}
.post .postTop .postTopLeft {
  display: flex;
  align-items: center;
}
.post .postTop .postTopLeft img {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  -o-object-fit: cover;
     object-fit: cover;
  cursor: pointer;
}
.post .postTop .postTopLeft .postUserName {
  margin: 0 15px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
}
.post .postTop .postTopLeft .postDate {
  font-size: 14px;
  color: #999;
}
.post .postCenter {
  padding: 10px;
}
.post .postCenter span {
  font-size: 20px;
}
.post .postCenter img {
  width: 100%;
  margin-top: 10px;
  border-radius: 10px;
  max-height: 600px;
  -o-object-fit: contain;
     object-fit: contain;
}
.post .postBottom {
  display: flex;
  justify-content: space-around;
}
.post .postBottom button {
  display: flex;
  align-items: center;
  padding: 7px;
  border: none;
  background-color: #dbe0e0;
  border-radius: 5px;
  margin-bottom: 10px;
  padding: 5px;
  cursor: pointer;
}
.post .postBottom button span {
  font-size: 15px;
  font-weight: 500;
  margin-left: 5px;
}
.post .postBottom button .fav {
  color: gray;
}
.post .postBottom button .love {
  color: red;
}
.post .comments {
  padding: 15px;
  width: 100%;
}
.post .comments.hide {
  display: none;
}
.post .comments .commentsContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.post .comments .commentsContainer .commentInput {
  width: 90%;
  background-color: white;
  padding: 5px;
  border-radius: 10px;
  border: 1px solid gray;
}
.post .comments .commentsContainer .commentInput input {
  width: 85%;
  border: none;
  outline: none;
  font-size: 20px;
  font-weight: 500;
}
.post .comments .commentsContainer .commentButton {
  margin-left: 20px;
  width: 10%;
}
.post .comments .commentsContainer .commentButton button {
  padding: 10px;
  border: none;
  background-color: #1877f5;
  color: white;
  font-size: 15px;
  font-weight: bolder;
  border-radius: 5px;
  cursor: pointer;
}/*# sourceMappingURL=Post.css.map */