.post {
  margin: 20px 0;
  box-shadow: 0 0 16px -8px rgba(0, 0, 0, 0.68),
    0 0 16px -8px rgba(0, 0, 0, 0.68);
  width: 100%;
  border-radius: 10px;
  margin: 15px;

  .postTop {
    display: flex;
    justify-content: space-between;
    margin: 10px;
    padding: 10px;
    align-items: center;

    .postTopLeft {
      display: flex;
      align-items: center;

      img {
        width: 34px;
        height: 34px;
        border-radius: 50%;
        object-fit: cover;
        cursor: pointer;
      }

      .postUserName {
        margin: 0 15px;
        font-size: 18px;
        font-weight: 600;
        cursor: pointer;
      }
      //! Post Date
      .postDate {
        font-size: 14px;
        color: #999;
      }
    }
  }
  .postCenter {
    padding: 10px;
    span {
      font-size: 20px;
    }

    img {
      width: 100%;
      margin-top: 10px;
      border-radius: 10px;
      max-height: 600px;
      object-fit: contain;
    }
  }

  .postBottom {
    display: flex;
    justify-content: space-around;

    button {
      display: flex;
      align-items: center;
      padding: 7px;
      border: none;
      background-color: #dbe0e0;
      border-radius: 5px;
      margin-bottom: 10px;
      padding: 5px;
      cursor: pointer;
      span {
        font-size: 15px;
        font-weight: 500;
        margin-left: 5px;
      }
      .fav {
        color: gray;
      }
      .love {
        color: red;
      }
    }
  }
  .comments {
    padding: 15px;
    width: 100%;
    &.hide {
      display: none;
    }
    .commentsContainer {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .commentInput {
        width: 90%;
        background-color: white;
        padding: 5px;
        border-radius: 10px;
        border: 1px solid gray;
        input {
          width: 85%;
          border: none;
          outline: none;
          font-size: 20px;
          font-weight: 500;
        }
      }
      .commentButton {
        margin-left: 20px;
        width: 10%;

        button {
          padding: 10px;
          border: none;
          background-color: #1877f5;
          color: white;
          font-size: 15px;
          font-weight: bolder;
          border-radius: 5px;
          cursor: pointer;
        }
      }
    }
  }
}
