.profile {
  display: flex;

  .profileRight {
    flex: 9;
    .profileRightTop {
      // padding: 15px;

      .profileCover {
        width: 100%;
        height: 350px;
        position: relative;
        .coverImg {
          width: 100%;
          height: 300px;
          max-height: 100%;
          object-fit: cover;
          border-radius: 8px;
          cursor: pointer;
        }

        .profileImg {
          width: 150px;
          height: 150px;
          border-radius: 50%;
          left: 0;
          right: 0;
          margin: auto;
          position: absolute;
          top: 200px;
          border: 3px solid white;
          z-index: 999;
          background-color: white;
          cursor: pointer;
        }
      }

      .profileInf {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h4 {
          font-size: 24px;
        }
        span {
          font-weight: 400;
        }
      }
    }
    .profileRightBottom {
      display: flex;
    }
  }
}
