.login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #bdbaba;

  .loginWrapper {
    width: 60%;
    height: 420px;
    display: flex;
    justify-content: space-between;
    .loginRight {
      flex: 1;
      background-color: white;
      padding: 15px;

      border-radius: 10px;
      .loginBox {
        display: flex;
        flex-direction: column;
        input {
          width: 100%;
          height: 50px;
          border-radius: 10px;
          padding-left: 20px;
          border: none;
          border: 3px solid gray;
          outline: 0;
          font-size: 24px;
          margin-bottom: 10px;
          padding-left: 20px;
          &:focus {
            border: 3px solid #1775ee;
          }
        }

        button {
          width: 100%;
          padding: 20px;
          height: 50px;
          margin: 15px 0;
          display: flex;
          justify-content: center;
          align-items: center;
          background-color: #1775ee;
          border: none;
          outline: none;
          border-radius: 10px;
          color: white;
          font-size: 24px;
          font-weight: bolder;
          cursor: pointer;
        }
        span {
          text-align: center;
          font-size: 16px;
          color: gray;
          cursor: pointer;
        }
        .account {
          background-color: #42b72a;
        }
      }
    }
    .loginLeft {
      flex: 1;
      display: flex;
      // align-items: center;
      flex-direction: column;
      justify-content: center;

      .titleName {
        font-size: 50px;
        color: #1775ee;
        font-weight: bolder;
        margin-bottom: 15px;
      }

      .titleDescription {
        font-size: 26px;
        margin-right: 10px;
      }
    }
  }
}
