::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background-color: #b3b3b3;
}

.sidebar {
  flex: 2.5;
  height: calc(100vh - 50px);
  overflow-y: scroll;
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
}
.sidebar .sidebarContainer .sidebarWrapper {
  padding: 20px;
}
.sidebar .sidebarContainer .sidebarWrapper .sidebarList {
  padding: 0;
  margin: 0;
  list-style: none;
}
.sidebar .sidebarContainer .sidebarWrapper .sidebarList .sidebarListItem {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  cursor: pointer;
  font-weight: 600;
}
.sidebar .sidebarContainer .sidebarWrapper .sidebarList .sidebarListItem span {
  font-size: 20px;
  margin-left: 15px;
}
.sidebar .sidebarContainer .sidebarWrapper .sidebarList button {
  width: 150px;
  border: none;
  padding: 10px;
  font-size: 20px;
  font-weight: 400;
  border-radius: 8px;
  cursor: pointer;
}
.sidebar .sidebarContainer .sidebarWrapper .sidebarList hr {
  margin: 20px 0;
  width: 70%;
  display: flex;
  justify-content: center;
}/*# sourceMappingURL=Sidebar.css.map */