.shareContainer {
  height: 176px;
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0 16px -8px rgba(0, 0, 0, 0.68), 0 0 16px -8px rgba(0, 0, 0, 0.68);
  margin: 15px;
}
.shareContainer .shareWrapper {
  padding: 10px;
}
.shareContainer .shareWrapper .topShare {
  display: flex;
  align-items: center;
}
.shareContainer .shareWrapper .topShare img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  -o-object-fit: cover;
     object-fit: cover;
  margin-right: 15px;
}
.shareContainer .shareWrapper .topShare input {
  width: 80%;
  border: none;
  outline: 0;
  font-size: 16px;
}
.shareContainer .shareWrapper hr {
  margin: 20px;
}
.shareContainer .shareWrapper .bottomShare {
  display: flex;
  margin-left: 20px;
  justify-content: space-between;
  align-items: center;
}
.shareContainer .shareWrapper .bottomShare .shareOptions {
  display: flex;
  justify-content: space-between;
}
.shareContainer .shareWrapper .bottomShare .shareOptions .shareOptionItem {
  display: flex;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
}
.shareContainer .shareWrapper .bottomShare .shareOptions .shareOptionItem .icons {
  font-size: 24px;
}
.shareContainer .shareWrapper .bottomShare .shareOptions .shareOptionItem span {
  margin-left: 8px;
  font-size: 18px;
  font-weight: 500;
}
.shareContainer .shareWrapper .bottomShare button {
  background-color: green;
  margin-right: 20px;
  color: #fff;
  padding: 7px;
  border: none;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bolder;
  font-size: 16px;
  transition: transform 0.4s ease-in-out;
}
.shareContainer .shareWrapper .bottomShare button:hover {
  transform: scale(1.1);
}/*# sourceMappingURL=share.css.map */