.shareContainer {
  height: 176px;
  width: 100%;
  border-radius: 10px;
  box-shadow: 0 0 16px -8px rgba(0, 0, 0, 0.68),
    0 0 16px -8px rgba(0, 0, 0, 0.68);
  margin: 15px;
  .shareWrapper {
    padding: 10px;
    .topShare {
      display: flex;
      align-items: center;
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        object-fit: cover;
        margin-right: 15px;
      }

      input {
        width: 80%;
        border: none;
        outline: 0;
        font-size: 16px;
      }
    }
    hr {
      margin: 20px;
    }

    .bottomShare {
      display: flex;
      margin-left: 20px;
      justify-content: space-between;
      align-items: center;
      .shareOptions {
        display: flex;
        justify-content: space-between;
        .shareOptionItem {
          display: flex;
          align-items: center;
          margin-left: 20px;
          cursor: pointer;

          .icons {
            font-size: 24px;
          }

          span {
            margin-left: 8px;
            font-size: 18px;
            font-weight: 500;
          }
        }
      }
      button {
        background-color: green;
        margin-right: 20px;
        color: #fff;
        padding: 7px;
        border: none;
        outline: none;
        border-radius: 5px;
        cursor: pointer;
        font-weight: bolder;
        font-size: 16px;
        transition: transform 0.4s ease-in-out;
        &:hover {
          transform: scale(1.1);
        }
      }
    }
  }
}
