.rightbarContainer {
  flex: 3.5;
  padding: 10px;
  margin: 10px;

  .birthday {
    display: flex;
    align-items: center;
    font-size: 15px;
    .icon {
      color: gold;
      font-size: 40px;
    }
    span {
      font-weight: bolder;
      margin: 0 5px;
    }
  }
  .adv {
    max-height: 300px;
    width: 100%;
    padding: 10px;
    border-bottom: 1px solid gray;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px;
    }
  }
  .onlineFriends {
    margin-top: 20px;

    h3 {
      margin: 10px;
      font-weight: bolder;
      font-size: 20px;
    }

    .list {
      padding: 10px;
      .listFriends {
        padding: 0;
        margin: 0;
        list-style: none;

        
      }
    }
  }
}
