.ProfileSidebar {
  flex: 3.5;
  padding: 10px;
  margin: 10px;
}
.ProfileSidebar .informaton h4 {
  margin-left: 10px;
  font-size: 26px;
  margin-bottom: 10px;
}
.ProfileSidebar .informaton .info {
  display: flex;
  margin: 10px;
  align-items: center;
}
.ProfileSidebar .informaton .info h5 {
  font-size: 20px;
  margin-right: 15px;
}
.ProfileSidebar .informaton .info span {
  font-size: 18px;
}
.ProfileSidebar .friendsWrapper {
  padding: 10px;
  border-top: 3px dashed gray;
  margin-top: 10px;
}
.ProfileSidebar .friendsWrapper h4 {
  font-size: 30px;
  margin-bottom: 10px;
}
.ProfileSidebar .friendsWrapper .friends {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
