.ProfileSidebar {
  flex: 3.5;
  padding: 10px;
  margin: 10px;
  .informaton {
    h4 {
      margin-left: 10px;
      font-size: 26px;
      margin-bottom: 10px;
    }

    .info {
      display: flex;
      margin: 10px;
      align-items: center;
      h5 {
        font-size: 20px;
        margin-right: 15px;
      }
      span {
        font-size: 18px;
      }
    }
  }

  .friendsWrapper {
    padding: 10px;
    border-top: 3px dashed gray;
    margin-top: 10px;
    h4 {
      font-size: 30px;
      margin-bottom: 10px;
    }
    .friends {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      
    }
  }
}
